body {
  margin: 0;
  padding: 0;
  background-color: #37322d;
  font-family: 'Seravek', -apple-system, BlinkMacSystemFont, 'Helvetica Neue', 'Helvetica', 'Source Han Sans', sans-serif;
  font-size: 1em;
  font-weight: 400;
  color: #c3c8c8;
}
h1,
h2,
h3 {
  font-size: 0.875em;
  font-weight: 400;
  color: #e1dcd7;
}
h1 {
  margin: 8px;
  text-align: center;
}
p {
  font-size: 0.875em;
  font-weight: 400;
}
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  font-size: 0.875em;
  cursor: pointer;
}
.navigator {
  position: fixed;
  width: 100vw;
  height: 36px;
  bottom: 0;
  left: 0;
  background-color: rgba(50, 50, 45, 0.5);
}
.navigator .navigator_content_wrapper {
  height: inherit;
  width: inherit;
  display: flex;
  justify-content: space-between;
}
.navigator .navigator_content_wrapper .navs {
  display: flex;
  justify-content: flex-start;
  height: 32px;
  max-width: 50%;
  overflow: hidden;
  margin: 2px;
  cursor: pointer;
}
.navigator .navigator_content_wrapper .tools {
  display: flex;
  justify-content: space-around;
  height: 32px;
  width: 50%;
  margin: 2px;
}
.navigator .navigator_content_wrapper .icon {
  cursor: pointer;
}
.navigator .navigator_content_wrapper .disabled {
  opacity: 0.125;
  cursor: not-allowed;
}
.navigator .navigator_content_wrapper .gallery-name {
  height: 32px;
  line-height: 32px;
  font-size: 0.875em;
  font-weight: 600;
}
.gallery-list li {
  margin: 8px;
}
.gallery-list li .title {
  color: #e1dcd7;
  text-align: center;
  height: 20px;
  line-height: 20px;
}
.photo {
  max-width: 100%;
  display: block;
  position: relative;
  outline: 1px solid #e1dcd7;
  outline-offset: -0.5px;
}
.photo img {
  display: block;
  width: 100%;
}
.caught {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}
.single_photo {
  position: relative;
  text-align: center;
  height: 100vh;
  overflow: hidden;
}
.single_photo .single_slide {
  position: relative;
}
.single_photo .single_slide .photo_wrapper {
  position: relative;
  margin: 16px;
  padding-bottom: 36px;
}
.single_photo .single_slide .photo_wrapper .photo {
  margin: 0 auto;
  box-shadow: 0 16px 32px rgba(5, 5, 0, 0.25);
  max-height: calc(100vh - 68px);
}
.single_photo .single_slide p {
  margin: 8px 0;
}
.single-gallery {
  text-align: center;
  margin: 8px 4px 36px 4px;
  min-height: 100vh;
}
.single-gallery li {
  position: relative;
}
.single-gallery li .photo {
  margin: 8px auto;
  box-shadow: 0 4px 8px rgba(50, 50, 45, 0.5);
}
