.logo {
  background-image: url(data:image/svg+xml;base64,);
  width: 22px;
  height: 32px;
  background-position: 0 6px;
  background-size: 21px 21px;
  background-repeat: no-repeat;
}
.main-logo {
  background-size: 42px 42px;
  background-position: center center;
  width: 44px;
  height: 44px;
  margin: 8px auto 0 auto;
}
.stack {
  position: relative;
  height: 200px;
  width: 200px;
  margin: 8px auto;
}
.stack .anton,
.stack .berta,
.stack .casar {
  display: block;
  outline: 2px solid #e1dcd7;
  outline-offset: -1px;
  box-shadow: 0 4px 8px rgba(5, 5, 0, 0.25);
  height: 160px;
  width: 160px;
  position: absolute;
  top: 20px;
  left: 20px;
  object-fit: cover;
}
.stack .berta {
  -webkit-transform: rotate(-6deg) translateZ(0);
          transform: rotate(-6deg) translateZ(0);
}
.stack .casar {
  -webkit-transform: rotate(12deg) translateZ(0);
          transform: rotate(12deg) translateZ(0);
}
.apply_wrapper .apply_button {
  display: block;
  text-align: center;
  margin: 16px auto;
  height: 30px;
  line-height: 30px;
  border-radius: 15px;
  width: 120px;
  box-shadow: 0 4px 8px rgba(5, 5, 0, 0.25);
  font-size: 0.875em;
  font-weight: 500;
}
.apply_wrapper .apply_button.apply_yellow {
  background-color: #ff7e00;
  color: #fff;
}
.apply_wrapper .apply_button.apply_blue {
  color: #c3c8c8;
  background-color: #0080ff;
}
.apply_wrapper .apply_button.apply_crazy {
  color: #c3c8c8;
  background: linear-gradient(270deg, #ff7e00, #0080ff);
  background-size: 200% 200%;
  -webkit-animation: DaNang 1s ease infinite;
          animation: DaNang 1s ease infinite;
}
.apply_wrapper .apply_content {
  text-align: start;
  text-indent: 1em;
  margin: 16px 4px;
  background: linear-gradient(270deg, #ff7e00, #0080ff);
  background-size: 200% 200%;
  -webkit-animation: DaNang 1s ease infinite;
          animation: DaNang 1s ease infinite;
  overflow: hidden;
  border-radius: 4px;
}
.apply_wrapper .apply_content .apply_content_inner {
  margin: 1px;
  padding: 8px;
  background-color: #37322d;
  border-radius: 3px;
}
.apply_wrapper .apply_memo p {
  margin: 0;
  line-height: 1.5;
  font-size: 0.75em;
  color: #c3c8c8;
}
@-webkit-keyframes DaNang {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes DaNang {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
